<template>
  <div>
    <h1>DevBlog</h1>
    <b-card
      v-for="post in posts"
      :key="post.id"
      :title="post.title"
      tag="article"
      style="max-width: 1080px;"
      class="mb-2 mx-auto bg-transparent"
    >
      <b-card-sub-title class="mb-2">Author: {{ post.author }}</b-card-sub-title>
      <b-card-text>
        {{ post.text }}
      </b-card-text>
      <b-card-footer>{{ post.date }}</b-card-footer>
    </b-card>
    <h2>Create Post</h2>
    <b-form class="d-flex flex-column mx-auto" style="max-width: 600px;" @submit="onSubmit" @reset="onReset" v-if="showForm">
      <b-form-input
        v-model="newPost.author"
        placeholder="Enter your name"
        class="mb-2 bg-transparent"
        required
      ></b-form-input>
      <b-form-input
        v-model="newPost.title"
        placeholder="Enter post title"
        class="mb-2 bg-transparent"
        required
      ></b-form-input>
      <b-form-textarea
        id="textarea"
        v-model="newPost.text"
        placeholder="Enter post text"
        rows="4"
        max-rows="12"
        class="mb-2 bg-transparent"
        required
      ></b-form-textarea>

      <b-button class="mx-auto mb-1" style="max-width: 100px;" type="submit" variant="primary">Submit</b-button>
      <b-button class="mx-auto mb-2" style="max-width: 100px;" type="reset" variant="danger">Reset</b-button>
    </b-form>
    <div class="d-flex flex-column">
      <router-link class="text-center mb-3" to="/about">About Page</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      posts: [],
      newPost: {
        author: "",
        title: "",
        text: ""
      },
      showForm: true
    }
  },
  async created() {
    this.refresh();
  },
  methods: {
    async refresh() {
      try {
        let response = await fetch(process.env.BASE_URL);
        if(!response.ok) throw Error(response.statusText);
        let data = await response.json();
        this.posts = data;
      } catch(e) {
        this.toast(e.message);
      }
    },
    async onSubmit(event) {
      event.preventDefault();
      try {
        let response = await fetch(process.env.BASE_URL, {
          method: "POST",
          body: JSON.stringify(this.newPost),
          headers: {
            'Content-type': 'application/json; charset=UTF-8'
          }
        });
        if(!response.ok) throw Error(response.statusText);
        await response.json();
        this.refresh()
        this.onReset(null)
        this.toast("Post created successfully", "success")
      } catch(e) {
        this.toast(e.message);
      }
    },
    onReset(event) {
      if(event) event.preventDefault();
      this.newPost.author = "";
      this.newPost.title = "";
      this.newPost.text = "";
      this.showForm = false;
      this.$nextTick(() => this.showForm = true);
    },
    toast(msg, variant = "danger") {
      this.$bvToast.toast(
        msg, 
        {
          title: variant == "danger" ? "Error" : "Success",
          variant: variant,
          solid: true
        }
      )
    }
  }
}
</script>

<style scoped>
</style>


